import { clone } from "../utils/CoreUtils";
import {
  PlayerContextState,
  PlayerContextTransform,
  PLAYER_INITIAL_STATE,
} from "./PlayerContext";

const retirementListeners: Set<PlayerContextTransform> = new Set();
const postRetirementListeners: Set<PlayerContextTransform> = new Set();

export function registerRetirementListener(listener: PlayerContextTransform) {
  retirementListeners.add(listener);
}

export function registerPostRetirementListener(
  listener: PlayerContextTransform,
) {
  postRetirementListeners.add(listener);
}

export function retire(state: PlayerContextState) {
  retirementListeners.forEach((listener) => {
    state = listener(state);
  });

  state.global.totalTimesResetted += 1;

  let newState = clone(PLAYER_INITIAL_STATE);
  newState.lastProcessedTime = Date.now();
  newState.global = state.global;
  postRetirementListeners.forEach((listener) => {
    newState = listener(newState);
  });
  return newState;
}
